﻿.fade.in {
  background-color: rgba(96, 98, 98, .5);
}

.modal {
  z-index: 3050 !important;
}
:global bbc-copper-modal {
  .modal-dialog {
    @media (min-width: 768px) {
      width: 50em;
      max-width:100%;
    }

    .modal-content {
      text-align: center;
			
			h1 {
				margin-top: 0;
				font-family: 'MuseoSans700';
				font-size: 2.5em;
				margin: 0 0.83em;
			}
			
			p {
				font-size: 0.9em;
				margin: 0;
				padding: 0 1em;
				line-height: 1.6em;
				font-family: 'MuseoSans300';
			}		
      
      border-radius: 0;
      background: #26C1B9 url(../app/bbc-copper-modal/images/family@2x.png) no-repeat;
      background-size: 280px auto;
      background-position: center 2em;
      color: white;
      box-shadow: 0 5px 20px 0px rgba(0,0,0,.5);
      .modal-header {
        button {
          position: absolute;
          top: 1.7em;
          right: 0.5em;
          opacity: 1;
          border: none;
          background: transparent;
          z-index: 100;
        }
        border-bottom: none;
      }
      .modal-body {
        padding-top : 150px;
        @media (min-width: 768px) {
          padding-left: 50px;
          padding-right: 50px;
        }
        h1 {
          color: white;
          margin-bottom: 20px;
        }
        p {
          font-family: 'MuseoSans300';
          color: white;
        }
      }
      .modal-footer {
        text-align: center;
        border-top: 0;
        padding: 1em 1em 3em;
        .get-fibre {
          padding: .4em;
          background: white;
          color: #36A8FC;
          border: none;
          border-radius: 1.5em;
          font-size: .875em;
          height: 2.5em;
          width: 14em;
          margin: auto;
        }
      }
    }
  }
  @media (max-width: 450px) {
    .modal-dialog {
      .modal-content {
        background: #26C1B9 url(../app/bbc-copper-modal/images/family-sm@2x.png) no-repeat;
        background-size: 271px auto;
        background-position: center 3em;
        .modal-header {
          button {
            img {
              width: 24px;
            }
          }
        }
        .modal-body {
          padding-top : 160px;
        }
      }
    }
  }
}




