﻿:global .bbc-terms {
  margin-top: 2em;
  background: #F4F4F2;
  .bbc-terms-header {
      text-align: center;
      padding: 0.25em 2em;
      p {
          font-size: 0.85em;
          margin-bottom: 0;
      }
      a {
          color: #009FE3;
          text-decoration: underline;
          &:hover {
              opacity: 0.8;
          }
      }
  }

  .bbc-terms-content {
    border-top: 1px solid #eee;
    padding: 2em;
    margin: 2em auto;
    h3 {
      text-align: left;
    }
    @media(max-width: 30em) {
      margin: 0;
    }
  }

  #bbc-terms-modal {
    .modal-dialog {
        overflow-y: initial !important;
        width: auto;
        max-width: 100%;
    }
    .modal-content {
      padding: 0 25px;
      margin-left: 10px;
      margin-right: 10px;
    }
    .modal-header {
      border-bottom: none;
      display: block;
      button {
        float: right;
        border: none;
        background: none;
        margin: 0;
        padding: 0;
        font-size: 21px;
        font-weight: 700;
        line-height: 1;
        color: #000;
        text-shadow: 0 1px 0 #fff;
        opacity: .5;
        &:hover{
          opacity: .2;
        }
      }
      h3 {
        font-family: 'MuseoSans300';
        font-size: 36px;
        margin-top: 25px;
        clear: both;
      }
    }
    .modal-body {
      height: 280px;
      overflow-y: auto;
      h4 {
        font-family: 'MuseoSans700';
        font-size: 16px;
        margin-bottom: 15px;
      }
      p {
        font-family: 'MuseoSans300';
        font-size: 16px;
      }
    }
    .modal-footer {
      border-top: none;
      height: 50px;
    }
    @media (min-width: 700px) {
      .modal-body {
        height: 600px;
      }
    }
    @media (min-width: 992px) {
      .modal-dialog {
        width: 800px;
      }
    }
  }
}




