﻿:global .bbc-no-address {
  .bbc-no-address-top {
    position: relative;
    min-height: 35em;
    background: transparent url(../app/bbc-no-address/images/bg-1023.jpg) no-repeat;
    border-bottom: 1px solid #ccc;
    background-size: cover;
    background-position: center;
    .overlay {
      background: rgba(0,0,0,0.35);
    }
    .wrapper {
      width: 800px;
      margin: 0 auto;
      max-width: 800px;
      text-align: center;
      color: white;
      @media only screen and (max-width: 800px) {
        width: 100%
      }
      .no-address-header {
        padding: 3.5em 1em 1.5em;
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);

        .overlay {
          background: none;
        }
        h1 {
          font-family: 'MuseoSans500';
          font-size: 2.5em;
          line-height: 1em;
          color: white;
          margin: 0;
        }
        h4 {
          font-family: 'MuseoSans300';
          font-size: 1.1em;
          margin-top: 0.5em;
          color: white;
          text-align: center;
        }
      }
      ul.options {
        list-style: none;
        margin: 0;
        padding: 0 2em;
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
        li {
          display: inline-block;
          width: 196px;
          position: relative;
          padding: 170px 0 3.5em;
          font-family: MuseoSans300;
          line-height: 1.4em;
          color: white;
          font-size: 1em;
          &.search {
            background: transparent url(../app/bbc-no-address/images/search.png) center 1em no-repeat;
            float: left;
          }
          &.map {
            background: transparent url(../app/bbc-no-address/images/map.png) center 1em no-repeat;
            margin: 0 auto;
          }
          &.form {
            background: transparent url(../app/bbc-no-address/images/form.png) center 1em no-repeat;
            float: right;
          }
          & > div {
            background-size: 124px;
            text-align: center;
          }
          .button-wrapper {
            position: absolute;
            bottom: 0;
            width: 100%;
            button {
              text-transform: uppercase;
              width: 100%;
              margin: 0 auto;
              text-shadow: none;
            }
          }
        }
      }
      .down-arrow {
        display: block;
        margin: .5em auto 0;
        outline: none;
        cursor: pointer;
        button {
          width: 41.9px;
          height: 77.8px;
          background: transparent url(../app/bbc-no-address/images/down-arrow.png) no-repeat center;
          border: none;
        }
      }
    }
    @media(max-width: 670px) {
      min-height: auto;
      background: none;
      border-bottom: none;
      .wrapper {
        .no-address-header {
          background: transparent url(../app/bbc-no-address/images/bg-670.gif) no-repeat;
          background-size: cover;
          background-position: center;
          padding: 3em 1em 2.5em;
          min-height: 10.7em;
          border-bottom: 1px solid #ccc;
          h1 {
            margin: 0;
          }
          h4 {
            margin-top: 1em;
          }
        }
        ul.options {
          background: white;
          padding: 0;
          text-shadow: none;
          li {
            color: $text-colour-primary;
            font-family: MuseoSans500;
            display: block;
            margin: 0 auto;
            &.search {
              float: none;
              margin-top: 2em;
            }
            &.map {
              margin-top: 2em;
            }
            &.form {
              float: none;
              margin-top: 2em;
            }
          }
        }
        .down-arrow {
          display: none;
        }
      }
    }
  }
}


