:global .chorus-shell-wrapper {

  h1 {
    @include h1();
  }
  h2 {
    @include h2();
  }
  h3 {
    @include h3();
  }
  h4 {
    @include h4();
  }
  p {
    margin: 0 0 1.7em;
    font-family: 'MuseoSans300';
    font-size: .875em;
    line-height: 1.71429em;
