﻿
#bbc-coverage-map {

  .text-top {
    max-width: 41em;
    margin: 0 auto;
    h2 {
      margin-top: 2em;
    }
    p {
      text-align: center;
      margin-bottom: 3em;
    }
  }

  .information-section {
    background: #f4f4f2;
    padding: 30px 0px;
  }

  .disclaimer {
    margin: 2em 0;
    font-size: 1em;

    .toggle-disclaimer-rounded {
      font-size: 1em;
      margin: 0.5em auto 1.5em;
      border-radius: 25px;
      border: 1px solid #009FE3;
      color: #009FE3;
      font-family: 'MuseoSans300';
      cursor: pointer;
      background-color: #009FE3;
      outline: none;
      box-shadow: none;
      height: 50px;
      width: 50px;

      line-height: normal;
      text-transform: none;
      text-align: center;
      span.expand {
        display: inline-block;
        text-align: center;
        background: transparent url(../app/bbc-step-dials/images/dis-plus.png) center no-repeat;
        background-size: 30px 30px;
        background-position: center;
        width: 100%;
        height: 100%;
      }
      &:hover {
        background-color: #009FE3;
        color: #009FE3;
        text-shadow: none;
      }
      &.active {
        span.expand {
          background: transparent url(../app/bbc-step-dials/images/dis-minus.png) center no-repeat;
        }
        &:hover {
          background-color: #009FE3;

        }
      }
    }

    .toggle-disclaimer {
      padding: .8em 2em;
      font-size: 1em;
      margin: 0.5em auto 1.5em;
      max-width: 26em;
      border-radius: 1.5em;
      border: 1px solid #009FE3;
      color: #009FE3;
      font-family: 'MuseoSans300';
      cursor: pointer;
      background-color: white;
      outline: none;
      box-shadow: none;
      height: auto;
      line-height: normal;
      text-transform: none;
      text-align: center;
      span.expand {
        display: inline-block;
        width: 16px;
        height: 15px;
        margin-left: 7px;
        background: transparent url(../app/bbc-step-dials/images/expand.png) center no-repeat;
        background-size: 30px 24px;
        background-position: 0px 0px;
        margin-bottom: -2px;
      }
      &:hover {
        background-color: #009FE3;
        color: white;
        text-shadow: none;
        span.expand {
          background-position: -15px 0px;
        }
      }
      &.active {
        &:hover {
          background-color: #009FE3;
          span.expand {
            background-position: -15px -15px;
          }
        }
        span.expand {
          background-position: 0 -15px;
        }
      }
    }
    &.active {
      .disclaimer-text {
        max-height: 25em;
        -webkit-transition: max-height 500ms ease-in;
        transition: max-height 500ms ease-in;
      }
    }
    .disclaimer-text {
      max-height: 0;
      overflow: hidden;
      -webkit-transition: max-height 500ms ease-out;
      transition: max-height 500ms ease-out;
      font-size: 1em;
    }
  }

  .disclaimer.current {
    background-color: #F4F4F2;
    padding: 2.5em 1em 2em 1em;
    h3 {

      font-family: 'MuseoSans300';
      font-size: 20px;
    }

  }

  .map-wrapper {
    max-width: 50em;
    margin: 0 auto;
  }

  .map-container {
    position: relative;
    overflow: hidden;
    -webkit-transition: height 1000ms;
    -moz-transition: height 1000ms;
    -o-transition: height 1000ms;
    transition: height 1000ms;
  }

  #layer-navigation {
    li.ADSL span { border-color: #FFF3B2 !important; }
    li.VDSL span { border-color: #FF8674 !important; }
    li.FIBRE span { border-color: #36A8FC !important; }
    li.UFB2 span { border-color: #36A8FC !important; }
    li.RURAL span { border-color: #2AD2C9 !important; }
  }

  .search {
    width: 100%;
    background-color: #009fe3;
    text-align: center;
    .input-holder {
      position: relative;
      border-radius: 1.5rem;
      box-shadow: 0 0.1rem 0.1rem 0 rgba(102, 102, 102, 0.3);
      display: inline-block;
      background: #ffffff;
      margin: 2rem;
      &::after {
        position: absolute;
        top: 0.65em;
        right: .8em;
        width: 21px;
        height: 20px;
        background: transparent url(../app/bbc-coverage-map/images/search.png) no-repeat;
        content: '';
      }
      input {
        width: 26em;
        padding: 0.5em 2em .5em 1.5em;
        font-size: 1.063em;
        background: transparent;
        font-family: 'MuseoSans300';
        border: none;
        outline: none;
      }
    }
  }

  .header {
    background-color: #22384E;
    color: #FFFFFF;
    height: 4em;
    #layer-navigation {
      float: left;
      width: 66%;
      ul {
        height: 4em;
        border-right: 1px solid #AAA;
        margin: 0;
        padding: 0;
        color: #AAAAAA;
        list-style-type: none;
        font-size: 1em;
        li {
          float: left;
          width: 18%;
          padding: 1.2em 0em 0;
          display: inline-block;
          text-align: center;
          color: #FFFFFF;
          &:hover,
          &.active {
            span {
              font-family: 'MuseoSans500';
            }
            &::after {
              content: '';
              position: absolute;
              left: calc(50% - 14px);
              top: 4em;
              width: 0;
              height: 0;
              border-left: 14px solid transparent;
              border-right: 14px solid transparent;
              border-top: 19px solid #22384E;
              clear: both;
              z-index: 3;
            }
          }
          &.active {
            span {
              border-bottom: 3px solid #36A8FC;
            }
          }
          span {
            padding: 0 .3em .3em;
            font-family: 'MuseoSans300';
          }
        }
      }
    }
    #fibre-slider-container {
      float: left;
      width: 33.3%;
      padding: .6em 5% 0;
      #fibre-date-range {
        width: 100%;
        background-color: transparent;
      }
      ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
        li {
          display: inline-block;
        }
      }
      #fibre-date {
        position: relative;
        font-size: .625em;
        font-family: 'MuseoSans300';
        li {
          margin-top: .5em;
          position: absolute;
          color: #AAAAAA;
          &:first-child {
            left: 0;
          }
          &:nth-child(2) {
            -ms-transform: translateX(-50%); /* IE 9 */
            -webkit-transform: translateX(-50%); /* Safari */
            transform: translateX(-50%);
            left: 50%;
          }
          &:last-child {
            right: 0;
          }
        }
      }
    }
  }

  #wivolo-map-wrapper {
    height: 27em;
    border: 0px solid grey;
    width: auto;
  }

  #legend {
    float: left;
    width: 33.3%;
    font-size: 1em;
    padding: 1.2em 1em 0 1em;
    text-align: center;
    img {
      margin: -0.3em .8em 0;
    }
    .legend-adsl {
      background-color: #FFF3B2;
    }

    .legend-vdsl {
      background-color: #FF8674;
    }

    .legend-rural {
      background-color: #2AD2C9;
    }
  }



  #menu {
    display: none;
    font-size: 2em;
    padding: 2em 2em 0 2em;
    .fa-angle-down {
      float: right;
    }
  }

  .display-none {
    display: none;
  }

  #location-details {
    background-color: #EBEBEB;
    .location-details-section:nth-child(1) {
      width: 100%;
    }
    .location-details-section:nth-child(2),
    .location-details-section:nth-child(2) ~ .location-details-section {
      width: 50%;
    }
    .location-details-section {
      float: left;
      width: 50%;
      font-size: .875em;
      padding: 2.5em 1em 2.5em 10%;
      text-align: left;
      .location-details-heading {
        font-family: 'MuseoSans700';
        margin-bottom: .4em;
      }
      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
          position: relative;
          font-family: 'MuseoSans300';
          padding-left: 2.5em;
          &:before {
            position: absolute;
            top: -.1em;
            left: 0;
            font-family: FontAwesome;
            content: '\f058';
            font-size: 1.4em;
            color: #009fe3;
          }
        }
      }
    }
  }


  .layer-type {
    cursor: pointer;
  }
  @media (min-width: 41em) {
    .map-container {
      padding: 0 !important;
    }
    .header {
      #layer-navigation {
        ul {
          li {
            bottom: 0 !important;
            position: relative !important;
          }
        }
      }
    }
  }
  @media (max-width: 41em) {
    .search {
      .input-holder {
        margin: 1.1em 0;
        width: 80%;
        input {
          width: 100%;
        }
      }
    }
    .header {
      background-color: #22384E;
      color: #FFFFFF;
      height: auto;
      #layer-navigation {
        float: none;
        width: 100%;
        ul {
          height: auto;
          border-right: 1px solid #AAA;
          margin: 0;
          padding: 0;
          color: #AAAAAA;
          list-style-type: none;
          font-size: 1em;
          li {
            position: inherit;
            float: none;
            display: block;
            height: 54px;
            width: 100%;
            margin: 0;
            padding: .58em 1.2em;
            font-size: 1.2em;
            text-align: left;
            border-bottom: 1px solid white;
            background-color: #22384E;
            color: white;
            &:hover,
            &.active {
              color: #FFFFFF;
              border-bottom: 1px solid white;
              span {
                font-family: 'MuseoSans500';
                border-bottom: none;
              }
              &::after {
                display: none;
              }
            }
            span {
              padding: 0;
              font-family: 'MuseoSans300';
            }
          }
        }
      }
      #fibre-slider-container {
        float: none;
        width: 100%;
        padding: .8em 5% 0;
        height: 4.2em;
        background: #52718e;
        #fibre-date-range {
          width: 100%;
          background-color: transparent;
        }
        ul {
          list-style-type: none;
          padding: 0;
          margin: 0;
          li {
            display: inline-block;
          }
        }
        #fibre-date {
          position: relative;
          font-size: .625em;
          font-family: 'MuseoSans300';
          li {
            margin-top: .5em;
            position: absolute;
            color: #FFF;
            &:first-child {
              left: 0;
            }
            &:nth-child(2) {
              -ms-transform: translateX(-50%); /* IE 9 */
              -webkit-transform: translateX(-50%); /* Safari */
              transform: translateX(-50%);
              left: 50%;
            }
            &:last-child {
              right: 0;
            }
          }
        }
      }
      #legend {
        float: none;
        width: 100%;
        font-size: 1em;
        padding: 1.2em 1em 1.2em 1em;
        text-align: left;
        background-color: #52718e;
        img {
          margin: -0.3em .8em 0;
        }
        .legend-adsl {
          background-color: #FFF3B2;
        }

        .legend-vdsl {
          background-color: #FF8674;
        }

        .legend-rural {
          background-color: #2AD2C9;
        }
      }
    }
  }

  /* Fibre Date Range Style */
  input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    margin: 6.1px 0;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 6.8px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #36a8fc;
    border-radius: 4.4px;
    border: 0.2px solid #36a8fc;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #0000ff, 0px 0px 0px #1a1aff;
    border: 3px solid #ffffff;
    height: 19px;
    width: 19px;
    border-radius: 15px;
    background: #36a8fc;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -6.3px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #36a8fc;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 6.8px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #36a8fc;
    border-radius: 4.4px;
    border: 0.2px solid #36a8fc;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 0px #0000ff, 0px 0px 0px #1a1aff;
    border: 3px solid #ffffff;
    height: 19px;
    width: 19px;
    border-radius: 15px;
    background: #36a8fc;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 6.8px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #36a8fc;
    border: 0.2px solid #36a8fc;
    border-radius: 8.8px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  input[type=range]::-ms-fill-upper {
    background: #36a8fc;
    border: 0.2px solid #36a8fc;
    border-radius: 8.8px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  input[type=range]::-ms-thumb {
    box-shadow: 0px 0px 0px #0000ff, 0px 0px 0px #1a1aff;
    border: 3px solid #ffffff;
    height: 19px;
    width: 19px;
    border-radius: 15px;
    background: #36a8fc;
    cursor: pointer;
    height: 6.8px;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #36a8fc;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #36a8fc;
  }
}


