$breakpoint: 700px;

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

:global .chorus-shell-wrapper {
  font-size: 16px;
  font-family: 'MuseoSans300';
  line-height: normal;
  color: $text-colour-primary;
  background: #fff;
  input {
    font-size: 1em;
    line-height: normal;
  }
  a {
    border-bottom: none;
  }
  .clearfix:after {
    content: "";
    display: table;
    clear: both;
  }

  .bb-column {
    max-width: 800px;
    margin: 0 auto;
  }

  .bb-container {
    margin: 0 auto;
    max-width: 800px;
    @media (max-width: 850px) {
      padding: 0 25px;
    }
  }

  .bb-video-wrapper {
    margin: 0 auto;
    background: #f4f4f2;
    padding: 3em;
    @media only screen and (max-width: $breakpoint) {
      padding: 3em 0;
    }
    .bb-container {
      padding: 0;
    }
  }

  .bb-step-title {
    font-family: 'MuseoSans100';
    font-size: 30px;
    margin-top: 80px;
    @media only screen and (max-width: $breakpoint) {
      margin-top: 20px;
    }
  }
  
  .fade-in {
    opacity: 1;
    transition: all linear 1s;
  }

  .fade-in.fade-out {
    opacity: 0;
    transition: none;
  }

}






