﻿:global .bbc-step-dials {
    padding: 0 1em;
    h2 {
      line-height: 1.3em;
    }
    &.bbc-step {
      .highlight-alt {
        margin-top :0;
      }
    }
	background-color: #fff;
    .dials-content {
      .dials-info {
        clear: both;
        padding: 0em 1em 2em 1em;
        margin: 0 auto;
        @media only screen and (max-width: 800px) {
          padding: 0 32px;
        }
        p {
          margin-top: 1em;
          text-align: center;
          font-size: 1em;
        }
        .view-map {
          margin-top: 29px;
          text-align: center;
          .btn-hollow {
            font-size: 1.125em;
          }
        }
      }

      .dials-wrapper {
        margin-bottom: 60px;
        .dial {
            font-size: 1em;
            text-align: center;
            float: left;

            h2 {
                text-transform: uppercase;
                font-family: 'MuseoSans300';
                color: #666;
                font-size: 1.125em;
                margin: .5em 0 .8em;
            }
        }


        /* one item */
        .dial:first-child:nth-last-child(1) {
          width: 100%;
        }

        /* two items */
        .dial:nth-last-child(2),
        .dial:nth-last-child(2) ~ .dial {
          width: 50%;
          @media (max-width: 37em){
            width: 100%;
            margin-bottom: 2em;
          }
        }

        @media (max-width: 40em){
            //.dial { font-size: 0.8em; }
        }

        @media (max-width: 32em){
            .dial {
                font-size: 0.95em;
                width: 100%;
            }
        }
        &:after {
          content: "";
          display: table;
          clear: both;
        }
      }

      .disclaimer {
        font-size: 1em;
        padding: 0 25px;
        .toggle-disclaimer-rounded {
          font-size: 1em;
          margin: 0.5em auto 1.5em;
          border-radius: 25px;
          border: 1px solid #009FE3;
          color: #009FE3;
          font-family: 'MuseoSans300';
          cursor: pointer;
          background-color: #009FE3;
          outline: none;
          box-shadow: none;
          height: 50px;
          width: 50px;

          line-height: normal;
          text-transform: none;
          text-align: center;
          span.expand {
            display: inline-block;
            text-align: center;
            background: transparent url(../app/bbc-step-dials/images/dis-plus.png) center no-repeat;
            background-size: 30px 30px;
            background-position: center;
            width: 100%;
            height: 100%;
          }
          &:hover {
            background-color: #009FE3;
            color: #009FE3;
            text-shadow: none;
          }
          &.active {
            span.expand {
              background: transparent url(../app/bbc-step-dials/images/dis-minus.png) center no-repeat;
            }
            &:hover {
              background-color: #009FE3;

            }
          }
        }

        .toggle-disclaimer {
          padding: .8em 2em;
          font-size: 1em;
          margin: 0.5em auto 1.5em;
          max-width: 26em;
          border-radius: 1.5em;
          border: 1px solid #009FE3;
          color: #009FE3;
          font-family: 'MuseoSans300';
          cursor: pointer;
          background-color: white;
          outline: none;
          box-shadow: none;
          height: auto;
          line-height: normal;
          text-transform: none;
          text-align: center;
          span.expand {
            display: inline-block;
            width: 16px;
            height: 15px;
            margin-left: 7px;
            background: transparent url(../app/bbc-step-dials/images/expand.png) center no-repeat;
            background-size: 30px 24px;
            background-position: 0px 0px;
            margin-bottom: -2px;
          }
          &:hover {
            background-color: #009FE3;
            color: white;
            text-shadow: none;
            span.expand {
              background-position: -15px 0px;
            }
          }
          &.active {
            &:hover {
              background-color: #009FE3;
              span.expand {
                background-position: -15px -15px;
              }
            }
            span.expand {
              background-position: 0 -15px;
            }
          }
      }
        &.active {
          .disclaimer-text {
            max-height: 22em;
            -webkit-transition: max-height 500ms ease-in;
            transition: max-height 500ms ease-in;
          }
        }
        .disclaimer-text {
          max-height: 0;
          overflow: hidden;
          -webkit-transition: max-height 500ms ease-out;
          transition: max-height 500ms ease-out;
          font-size: 1em;
          text-align: center;
        }
      }
    }

  .disclaimer.current {
    background-color: #F4F4F2;
    padding: 2.5em 1em 2em 1em;
    margin: 0 auto;
    @media only screen and (max-width: $breakpoint) {
      padding: 1.875em 1em 1.5em 1em;
    }

    h3 {

      font-family: 'MuseoSans300';
      font-size: 20px;
    }
  }
}


