﻿:global .bbc-step-process {
  .intro.lead {
    margin-bottom: 1.5em;
  }

  .content {
    margin-top: 3em;
  }

  .process {
    max-width: 40em;
    p {
      font-size: 1em;
    }
  }

  .process-list {
    margin: 4em auto;
    max-width: 75em !important;
    ul {
      list-style: none;
      margin: 0 auto;
      padding: 0 24px;
      max-width: 1100px;
      li {
        text-align: center;
        h4 {
          text-align: center;
          margin-bottom: 1em;
          font-size: 1.2em;
        }
        p {
          font-family: 'MuseoSans100';
          font-size: 1em;
        }

        @media only screen and (min-width: $breakpoint) {
          display: inline-block;
          width: 33%;
          padding: 1em 3em;
          &.agree {
            float: left;
          }
          &.build {
            margin: 0 auto;
          }
          &.connect {
            float: right;
          }
        }

      }
    }
  }
  .process-icon {
    width: 175px;
    height: 175px;
    padding: 1em;
    @media only screen and (min-width: $breakpoint) {
      max-width: 75%
    }

    .bb-video-wrapper {
      p {
        font-family: 'MuseoSans500';
        font-size: 1em;
      }
    }
  }
}





