[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

:global .chorus-shell-wrapper {
  min-height: 100vh;
  .section-highlighted {
    overflow: hidden;
    padding: 0;
    //border-bottom: 1px solid rgba(0,0,0,0.025);
  }
  .section-highlighted .incidents-content {
    background: #fff;
    padding: 3em 4em;
    box-shadow: 0 0.2em 0.4em 0 rgba(185, 185, 185, 0.3);
    margin: 0 0 3em 0;
    position: relative;
  }
  .section-highlighted .note {
    margin: 0;
  }
  .section-highlighted .note p {
    font-family: 'MuseoSans300';
    line-height: 1.3em;
    font-size: 1.1em;
    margin: 0.5em 0;
  }
  .section-highlighted .note h2 {
    font-size: 1.4em;
    line-height: 1em;
    margin: 0;
  }
  .note h1, .note h2, .note h3, .note h4 {
    color: #009FE3;
    margin-top: 0;
  }







  .entry-header {
    text-shadow: 0 0.2em 0.4em rgba(0,0,0,0.5);
    color: #fff;
    height: auto !important;
  }
  .entry-header > div {
    position: relative !important;
    overflow: hidden;
    padding: 4em 3.75em 3em 3.75em;
    margin: 0;
    bottom: 0 !important;
  }
  .entry-header > div .title {
    font-size: 3em;
    margin: 0 auto;
    padding: 0 !important;
    line-height: 1em !important;
  }
  .entry-header > div p {
    max-width: 32em;
    padding: 1.5em;
    margin: 0 auto;
  }
  .entry-header > div .search-address {
    max-width: 100%;
    width: 34em;
    padding: 0.75em;
    margin: 0;
  }

  @media (max-width: 63.9em) {
    .entry-header > div { padding: 4em 1em 3em 1em; }
  }





  .load-error {
    border-left: 0.5em solid #d25;
    background-color: #fff;
    box-shadow: 0 0.2em 0.4em 0 rgba(185, 185, 185, 0.3);
    padding: 1em 1.2em;
    margin: 0 0 3em 0;
  }
  .load-error h3 {
    color: #d25;
    margin: 0 0 0.5em 0;
  }
  .load-error p {
    font-family: 'MuseoSans300';
    margin: 0 0 0.8em 0;
  }
  .load-error button {
    color: #d25;
    border-color: #d25;
    background-color: #fff;
    text-transform: uppercase;
    padding: 0.5em 0.8em;
    opacity: 0.5;
  }
  .load-error button:hover {
    opacity: 1;
  }


  .faqs-content {
    margin-top: 3.5em;
  }
  .faqs-content .accordion {
    max-width: 55em;
    font-size: 0.95em;
  }




  .accordion {
    box-shadow: 0 0.2em 0.4em 0 rgba(185, 185, 185, 0.3);
    font-size: 1em;
  }
  .accordion .q,
  .accordion .a,
  .accordion .drawer.active .a {
    margin: 0;
    padding: 1.4em 1.8em;
    border-bottom: 1px solid #F2F2F2;
    overflow: hidden;
    height: auto;
    position: relative;
    transition: 0.15s all;
    color: #888;
  }
  .accordion .q {
    padding-right: 3.5em;
    cursor: pointer;
  }
  .accordion .q:hover {
    background: #f6f8fa;
  }

  .accordion .drawer.active .q {
    color: #333;
  }
  .accordion .a {
    font-family: 'MuseoSans300';
    height: 0;
    padding-top: 0;
    padding-bottom: 0;
    border-width: 0;
    color: #aaa;
    font-size: 0.95em;
  }
  .accordion .q::after {
    font-family: FontAwesome;
    content: "\f078";
    font-size: 1.4em;
    position: absolute;
    right: 1.15em;
    top: 50%;
    line-height: 0
  }
  .accordion .drawer.active .q::after {
    content: "\f077";
  }





  .breadcrum {
    border-bottom: 1px solid #eaeaea;
    padding: 1em 0;
  }
  .breadcrum a,
  .breadcrum span,
  .breadcrum a {
    font-size: 0.8em;
    color: #aaa;
  }
  .breadcrum span {
    color: #aaa;
    font-family: 'MuseoSans700';
    color: #009FE3;
  }
  .breadcrum .separator {
    font-size: 0.5em;
    display: inline-block;
    padding: 0 1em;
    position: relative;
    top: -0.05em;
    color: #aaa;
  }



  .debug-toggle {
    color: #000;
    opacity: 0.025;
    position: absolute;
    bottom: 0.75em;
    right: 0.75em;
    cursor: pointer;
    line-height: 1em;
  }
  .debug-toggle:hover {
    opacity: 0.2;
  }





  @media (max-width: 50em) {
    .section-highlighted .incidents-content { padding: 2em 1.5em; }
  }

  @media (max-width: 30em) {
    .section-highlighted .incidents-content { padding: 2em 1em; }
  }



}




