﻿.modal {
  &.fade {
    background-color: rgba(96, 98, 98, .5);
    //opacity: 0;
  }
  .active {
    display: block;
  }
}
:global .bbc-submit-address {
  .modal-dialog {
    .modal-content {
      background: white;
      color: #4A4A4A;
      .modal-header {
        border-bottom: none;
        button.close-modal {
          position: absolute;
          top: 1.5em;
          right: 1em;
          opacity: 1;
          border: none;
          background: transparent;
          z-index: 100;
        }
      }
      .modal-body {
        padding: 2em 4em 4em;
        text-align: left;
        h1 {
          color: #4A4A4A;
          font-size: 1.7em;
          text-align: left;
          font-family: 'MuseoSans300';
          line-height: 1.5em;
        }
        p {
          font-family: 'MuseoSans300';
          margin-top: 1.5em;
          font-size: .9em;
          line-height: 1.6em;
        }
        form {
          position: relative;
          #address-error {
            margin-top: .2em;
            color: red;
          }
          label {
            width: 100%;
            margin: 0.75em 0
          }
          input {
            width: 100%;
            border: 1px solid $input-border;
            padding: 1.1em 1.3em;
            font-size: 1em;
            &::-webkit-input-placeholder,
            &::-moz-placeholder,
            &:-ms-input-placeholder {
              color: $input-placeholder;
              font-family: 'MuseoSans300';
            }
          }

          .ar-suggestions {
            position: absolute;
            list-style: none;
            padding: 0;
            margin: .3em 2% 0;
            font-size: 1em;
            width: 100%;
            z-index: 100;
            margin: 0;
            border: 1px solid $input-border;
            max-height: 5em;
            overflow-y: scroll;
            .results & {
              margin: 0;
              width: 100%;
              border: 1px solid $input-border;
            }
            li {
              width: 100%;
              padding: 0.7em;
              border-bottom: 1px solid $input-border;
              outline: none;
              cursor: pointer;
              background-color: rgba(255,255,255,.88);
              font-family: 'MuseoSans300';
              color: #3B3B3B;
              border-top: none;
              margin: 0;
              &.active,
              &:hover{
                background-color: #009FE3;
                color: white;
              }
            }
          }
          button.cancel {
            border: 1px solid $input-border;
            text-transform: capitalize;
          }
          .form-actions {
            padding-top: 50px;
            button {
              width: 11em;
              &.cancel {
                border: 1px solid $chorus-blue-border;
              }
            }
          }
        }
      }
    }
  }
  @media (max-width: 450px) {
    .modal-dialog {
      .modal-content {
        .modal-header {
          button {
            img {
              width: 24px;
            }
          }
        }
        .modal-body {
          padding : 1em 2em 2em;
          form {
            .form-actions {
              button {
                width: 8em;
                margin: 0;
                float: right;
                &.cancel {
                  float: left;
                }
              }
            }
          }
        }
      }
    }
  }
}



