@-webkit-keyframes spin
{
  0%   {-webkit-transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);}
}
@keyframes spin
{
  0%   {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

$breakpoint: 700px;

:global .bbc-address {
  position: relative;
  background: #192636 url('../app/bbc-address/images/shoes.jpg') center/cover;
  height: 100vh;

  &.landing {
    @media (min-width: 768px) {
      .bbc-address-input {
        margin-top: -300px;
        padding-top: 0;
      }
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
    }
  }

  &.results,
  &.loading,
  &.refined {
    height: auto;
  }
  .bbc-address-map {
    display: none;
  }
  &.results {
    background: none;
    .bbc-address-map {
      display: block;
      height: 150px;
      @media (min-width: $breakpoint) {
        height: 250px;
      }
    }

    .bbc-address-autocomplete {
      margin-left:0;
    }

    .bbc-address-input {
      position: relative;
      top: 0; right: 0; left: 0;
      background-color: #7D55C7;
      padding: 20px 0 1px;
      transform: none;
      color: #fff;

      &::after {
        position: absolute;
        bottom: -16px;
        left: 50%;
        margin-left: -14px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 17px 12.5px 0 12.5px;
        border-color: #7D55C7 transparent transparent transparent;
        content: '';
        z-index: 10;
      }
      .wrapper{
        padding: 0 20px !important;
        @media (min-width: 992px) {
          padding: 0 50px !important;
        }
        .search-group {
            margin-top: 18px;
            margin-bottom: 14px;
        }
      }

      @media only screen and (min-width: $breakpoint) {
        .wrapper {
          position: absolute;
          bottom: 0;
          right: 0;
          .search-group {
            margin-top: 19px;
            margin-bottom: 32px;
          }
        }
      }
      @media (min-width: 992px) {
        .wrapper .search-group {
          margin-top: 27px;
          margin-bottom: 41px;
        }
      }
    }
    .results-heading {
          h2,p {
            text-align: left; 
            text-shadow: none;
            opacity: 0.6;
            padding-left: 2px;
          }
          h3 {
            text-align: center !important;
            font-family: 'MuseoSans500' !important;
            letter-spacing: -0.2px !important;
            font-size: 28px !important;
            font-weight: normal !important;
            margin-bottom: 18px !important;
            @media only screen and (min-width: $breakpoint) {
              text-align: left !important;
              font-size: 32px !important;
              letter-spacing: -0.5px !important;
              margin-bottom: 4px !important;
            }
            @media (min-width: 992px) {
              font-size: 38px !important;
              letter-spacing: -0.6px;
            }
          }
        }
    @media only screen and (min-width: $breakpoint) {
      .bbc-address-map {
        float: left;
        width: 35%;
      }
      .bbc-address-input {
        float: left;
        width: 65%;
        height: 250px;
        border: none;
        padding-right: 0;
        &::after {
          left: auto !important;
          right: -25px !important;
          bottom: auto !important;
          top: 50% !important;
          margin-left: 0 !important;
          margin-top: -8px !important;
          border-width: 16px 0 16px 25px !important;
          border-color: transparent transparent transparent #7D55C7 !important;
        }
        .wrapper {
          padding: 0 1em;
          max-width: 38.25em;
          float: right;
        }        
      }
    }
    @media (min-width: 992px){
      .bbc-address-map {
        width: 50%;
        height: 300px;
      }
      .bbc-address-input {
        width: 50%;        
        height: 300px;
      }
    }
    .results-heading {
      p{
        font-size: 14px !important;
        display: none !important;
      }       
      @media only screen and (min-width: $breakpoint) {
        padding-left: 15px;
        p{
          display: inline !important;
        }
      }     
    }
    .results-change-address {
        p {
          font-family: 'MuseoSans300' ;
          font-style: italic;
          font-size: 14px;
          text-align: center;
          margin: 14px 0;
          letter-spacing: -0.4px;
          opacity: 0.75;
          &:hover{
            color:  #333;
            cursor: pointer;
          }
        }
        @media (min-width: $breakpoint)
        {
          padding-left: 25px;
          p{
            text-align: left;
          }
        }
      }
  }

  .bbc-address-input {
    padding-top: 50px;

    .wrapper {
      width: 100%;
      max-width: 43em;
      margin: 0 auto;
      position: relative;
      padding: 0 25px;
    }

    h2, h3, h4, p {
      color: #fff;
      font-family: 'MuseoSans300';
      text-align: center;
      max-width: 600px;

      margin: 0.4em auto;
      padding: 0;
      line-height: 1.4em;
    }

    h2 {
      font-size: 2.5em;
      line-height: 1em;
    }

    h3 {
      font-size: 2.625em;
      line-height: 1em;
      font-weight: bold;
    }

    h4 {
      font-family: 'MuseoSans500';
      font-size: 1em;
      letter-spacing: 1px;
    }

    p {
      font-family: 'MuseoSans100';
      font-size: 1em;
      margin-bottom: 1.5em;
    }

    @media (max-width: 30em) {
      h2 { font-size: 2.1em; }
      padding-top: 30px;
      h4 { display: none; }
      h3 { font-size: 2em; }
      p {
        font-size: 1em;
        font-family: 'MuseoSans500';
        margin-top: 24px;
      }

    }
  }

  .loading-text {
    position: absolute;
    display: none;
    z-index: 1000;
    width: 100%;
    background: #3571C5;
    height: 14em;
    text-align: center;
    &:after {
      width: 100px;
      height: 100px;
      position: absolute;
      left: calc(50% - 50px);
      top: calc(50% - 50px);
      background: url(../app/bbc-address/images/spinner.png) center no-repeat;
      content: '';
      -webkit-animation: spin 2.5s infinite linear;
      animation: spin 2.5s infinite linear;
    }
    &.active {
      display: block;
    }
  }

  .bbc-address-list {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    background: transparent url(../app/bbc-no-address/images/bg-1023.jpg) no-repeat;
    color: white;
    text-align: center;
    font-family: 'MuseoSans500';
    z-index: 10;
    h1 {
      font-family: 'MuseoSans500';
      font-size: 2.6em;
      color: white;
      line-height: 1.3em;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
      margin: 0;
      padding-top: 1em;
    }
    p {
      padding: 0;
      margin: 0 auto;
      max-width: 40em;
      color: white;
      font-size: 1.1em;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    }
    .bbc-address-list-input {
      position: relative;
      padding: 1.5em 1.6em 2.7em;
      .wrapper {
        position: relative;
        width: 100%;
        max-width: 30em;
        padding: 0;
        margin: auto;
        .items {
          margin-right: 50px;
          border: 1px solid #bbb;
          overflow: auto;
          background: rgba(255,255,255,.9);
          height: 13.3em;
          -webkit-box-shadow: 0px 3px 2px 0px rgba(88, 91, 93, 0.7);
          -moz-box-shadow:    0px 3px 2px 0px rgba(88, 91, 93, 0.7);
          box-shadow:         0px 3px 2px 0px rgba(88, 91, 93, 0.7);
          .item {
            color: #3B3B3B;
            padding: 0.75em 1.8em;
            border-bottom: 1px solid #bbb;
            font-size: 1em;
            font-family: 'MuseoSans300';
            cursor: pointer;
            text-align: left;
            &:hover {
              background-color: #009FE3;
              color: white;
            }
            &.no-address {
              &:hover {
                background-color: transparent;
                color: #3B3B3B;
              }
            }
          }
        }
        .clear-address {
          float: right;
          display: block;
          width: 50px;
          padding: 0.5em;
          height: 2.453em;
          border: 1px solid #bbb;
          border-left: 0;
          border-radius: 0;
          font-size: 1.125em;
          text-align: center;
          cursor: pointer;
          background: rgba(255,255,255,.9);
          span {
            font-size: 1.4em;
            color: #999;
          }
          &:focus {
            border-color: #bbb;
          }
        }
      }

    }
  }

}
@media (max-width: 30em) {
  :global .bbc-address {
    background: #192636 url('../app/bbc-address/images/shoes-sm@2x.jpg') center/cover no-repeat;
    //height: 27em;
  }
}


