﻿:global .bbc-progress-bar {
	$breakpoint: 1200px;
	position: relative;
	background: #EBEBEB;
	box-shadow: 5px 2px 3px #aaa;
	@media only screen and (min-width: $breakpoint) {
		background: #fff;
	}
	> div {
		text-align: center;
		height: 76px;
		padding-top: 18px;
		margin: 0 auto;
		@media only screen and (min-width: $breakpoint) {
			height: 115px;
			padding-top: 35px;
		}
	}
	.change-state-btn {
		position: absolute;
		top: 23px;
		width: 30px;
		height: 30px;
		background: #fff;
		border: 2px solid #35a6e8;
		border-radius: 50%;
		color: #d4d4d4;
		cursor: pointer;
		.fa {
			color: #35a6e8;
		    font-size: 16px;
		    margin-top: 5px;
		}
		&.prev {
			left: 14px;
			margin-left: -2px;
		}
		&.next {
			right: 14px;
			margin-left: 2px;
		}
		@media only screen and (min-width: $breakpoint) {
			display: none;
		}
	}
	.progress-step {
		display: none;
		cursor: pointer;
		&.current {
			position: relative;
			display: inline-block;
		}
		&:hover {
			color: #35a6e8;
		}

		&:first-child {
			margin-left: 0;
		}
		@media only screen and (min-width: $breakpoint) {
			display: inline-block;
			margin-left: 22px;
			margin-right: 22px;
		}
	}
	.circle-image {
		float: left;
		div {
			width: 50px;
			height: 50px;
		}
		.image-1 {
			background-image: url(../app/bbc-progress-bar/images/circle_1_active.svg);
		}
		.image-2 {
			background-image: url(../app/bbc-progress-bar/images/circle_2.svg);
		}
		.image-3 {
			background-image: url(../app/bbc-progress-bar/images/circle_3.svg);
		}
		.image-4 {
			background-image: url(../app/bbc-progress-bar/images/circle_4.svg);
		}
		.image-5 {
			background-image: url(../app/bbc-progress-bar/images/circle_5.svg);
		}
	}	

	.visited {
		.image-1 {
			background-image: url(../app/bbc-progress-bar/images/circle_1_done.svg);
		}
		.image-2 {
			background-image: url(../app/bbc-progress-bar/images/circle_2_done.svg);
		}
		.image-3 {
			background-image: url(../app/bbc-progress-bar/images/circle_3_done.svg);
		}
		.image-4 {
			background-image: url(../app/bbc-progress-bar/images/circle_4_done.svg);
		}
		.image-5 {
			background-image: url(../app/bbc-progress-bar/images/circle_5_done.svg);
		}
	}

	.current {
		.image-1 {
			background-image: url(../app/bbc-progress-bar/images/circle_1_active.svg);
		}
		.image-2 {
			background-image: url(../app/bbc-progress-bar/images/circle_2_active.svg);
		}
		.image-3 {
			background-image: url(../app/bbc-progress-bar/images/circle_3_active.svg);
		}
		.image-4 {
			background-image: url(../app/bbc-progress-bar/images/circle_4_active.svg);
		}
		.image-5 {
			background-image: url(../app/bbc-progress-bar/images/circle_5_active.svg);
		}
	}

	.title {
		float: left;
		font-family: 'MuseoSans500';
		font-size: 0.875em;
		margin: 19px 0px 0px 15px;
	}

	.current::before {
		position: absolute;
		bottom: -13px;
		left: 50%;
		width: 23px;
    	height: 18px;
		margin-left: -11px;
		content: '';
		background: url(../app/bbc-progress-bar/images/progress-bar-current-ind.png) 0 0 no-repeat;
		background-size: 23px 18px;
		@media only screen and (min-width: $breakpoint) {
			bottom: -35px;
			margin-left: -19px;
		}
	}
}



