﻿:global .bbc-step-providers {
  &.vdsl {
    .tabs {
      display: none;
    }
    .panel {
      margin-top: 0;
    }
  }
  .tabs {
    height: 20px;
    margin-top: 5px;
    padding: 0;
    overflow: hidden;

    .tab {
      border-radius: 0;
      border: none;
      background-color: #f6f8fa;
      color: #009FE3;
      width: 50%;
      float: left;
      padding: 1em;
      text-align: center;
      cursor: pointer;

      &:hover {
        background-color: #f2f4f8;
        border-top-color: #f2f4f8;
      }
    }

    .tab.active {
      background-color: #7D55C7;
    }
  }

  .panel {
    .highlight.instructions:before {
      border-bottom-color: #F4F4F2;
      top: auto;
      bottom: -0.25em;
    }

    .providers {
      .providers-buttons-wrapper {
        padding-bottom: 2em;

        button {
          text-transform: none;
          margin: 1em;
        }
      }

      .providers-center-wrapper {
        max-width: 55em;
        margin: auto;
      }
      overflow: hidden;
      // margin-top: 2em;
      background-color: #F4F4F2;
      padding: 4em 0;
      a.provider {
        display: inline-block;
        //width: 152px;
        //height: 124px;
        background-color: #fff;
        margin: 15px;

        &:hover {
          opacity: 0.8;
        }

        img {
          display: block;
          width: 152px;
          height: auto;
          margin: 0.5em auto;
        }
      }

      @media (max-width: 425px) {
        a.provider img{
          width: 110px;
        }
      }

      @media (max-width: 768px) {
        padding-top: 1em;
      }

    }
  }

  .disclaimer {
    text-align: center;
    width: 100%;
    color: #999;
    float: left;

    padding-top: 2.5em;
    p {
      max-width: 40em;
      font-size: 0.8em;
      margin: auto;
    }
  }

  .highlight-alt {
    margin-top: -20px !important;
    background-color: $purple-color;
  }
}



