:global .bbc-vdsl-potential {

  margin-top: 0em;
  display: block;
  h3 {
    margin-bottom: 0.5em;
    font-size: 1.125em !important;
  }

  $barHeight: 38px;
  $handleHeight: 36px;
  $handleWidth: 8px;

  .vdsl-full-range {
    width: 225px;
    height: 52px;
    position: relative;
    padding: 0 14px 0 17px;
    margin: 5em auto 40px;
    background-image: url(../app/bbc-vdsl-potential/images/range_low@2x.png);
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    &.range-high {
      background-image: url(../app/bbc-vdsl-potential/images/range_high@2x.png);
    }

    @media screen and (min-width: 375px) {
      width: 270px;
      height: 63px;
      padding: 0 19px;
    }

    @media screen and (min-width: 480px) {
      width: 396px;
      height: 63px;
      padding: 0 19px;
      background-image: url(../app/bbc-vdsl-potential/images/range_low_lge@2x.png);
      &.range-high {
        background-image: url(../app/bbc-vdsl-potential/images/range_high_lge@2x.png);
      }
    }


    .vdsl-tooltip {
      $tooltipWidth: 155px;
      $tooltipHeight: 45px;
      $tooltipIndicatorheight: 7px;
      position: absolute;
      top: -($tooltipHeight + $tooltipIndicatorheight + 1);
      margin-left: -($tooltipWidth/2 + $handleWidth/2) + 3;
      width: $tooltipWidth;
      height: $tooltipHeight;
      padding: 9px 5px 12px;
      background-image: url(../app/bbc-vdsl-potential/images/info.png);
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 155px 45px;
      border-radius: 7px;
      text-align: center;
      font-size: 16px;
      color: #fff;
    }
    .range-con {
      position: relative;
    }
    .vdsl-range {
      position: absolute;
      top: -($handleHeight - $barHeight)/2;
      margin-left: -($handleWidth) / 2;
      width: $handleWidth;
      height: 30px;
      background: rgba(5, 123, 221, 0.66);
      @media screen and (min-width: 375px) {
        height: $handleHeight;
      }
    }
  }
}


