:global .chorus-beta-feedback {
	background: #fe4;
	color: #000;
	text-shadow: none;
	border-radius: 0.25em;
	box-shadow: 0 0.1em 0.5em rgba(0,0,0,0.5);
	position: relative;
	text-align: left;
	top: -3em;
	padding: 0.8em 12.5em 0.8em 2.7em;
	max-width: 76.4em;
	margin: auto;

	p.text {
		padding: 0 !important;
		margin: 0 !important;
		max-width: 100% !important;
		
		i.fa {
			position: absolute;
			left: 0.6em;
			top: 0.7em;
			font-size: 1.3em;
		}
	}
	
	button {
		position: absolute;
		right: 1em;
		top: 0.7em;
		padding: 0.48em 0.88em;
		background: #fe4;
		border: 1px solid #000;
		color: #000;
		text-shadow: none;
		font-size: 0.88em;
		outline: none;
	}

	button:hover {
		background: #000;
		color: #fe4;
	}
}



@media (max-width: 40em) {
	:global .chorus-beta-feedback { font-size: 0.8em; }
}



@media (max-width: 32em) {
	:global .chorus-beta-feedback { 
		padding: 1em 1.5em;
		text-align: center;
		
		p.text i.fa{ 
			position: static;
			font-size: 1em;
		}

		button {
			position: static;
			margin-top: 0.5em;
		}
	}
}


