.results .input-wrap {
  .fa {
    display: none;
  }

  .fa-times {
    right: 0.5em !important;
    top: 7px;
    left: auto !important;
  }

  .btn-address-search-action {
    display: none;
  }


}

:global .bbc-address-autocomplete {
  position: relative;
  .results & {
    @media (min-width: 700px){
      max-width: 510px;
    }
    margin: 0;
    input {
      float: none;
      height: 40px;
      width: 100%;
      padding: .5em 37px .5em 1em;
      border-width: 1px;
      font-size: 14px;
      font-family: 'MuseoSans500';
      color: #000;
      background: #F8F8F8;
      &:focus {
        color: #000;
        border-color: #bbb;
      }
      @media (min-width: 700px) {
        height: 50px;
      }
    }
    .bbc-address-loading {
      right: 0.8em;
      top: 0.9em;
      left: auto;
    }
  }
  .input-wrap {
    position: relative;
    text-align: center;

    .fa-times {
      position: absolute;
      top: 12px;
      left: 435px;
      font-size: 25px;
      color: #36a8fc;
      font-weight: normal;
      display: none;
    }
  }
  input {
    position: relative;
    height: 50px;
    width: 100%;
    padding: .7em 1.7em .7em 1.3em;
    font-size: 1em;
    border-radius: 25px;
    border: 1px solid #fff;
    &:focus {
      outline: none !important;
      border-color: #009fe3;
    }
  }

  .bbc-address-loading {
    color: #009fe3;
    position: absolute;
    z-index: 2;
    left: 435px;
    top: 13px;
    .fa {
      font-size: 1.4em;
    }
  }
}

.ar-suggestions {
  display: block;
  position: absolute;
  list-style: none;
  padding: 0;
  margin: 2px 15px 0;
  font-size: 1em;
  // width: calc(100% - 30px);
  width: 440px;
  z-index: 100;
  &.jquery {
    &.mobile {
      display: none;
      font-size: 16px;
      line-height: 1.13em;
    }
    @media (max-width: 30em) {
      display: none;
      &.mobile {
        display: block;
      }
    }
  }
  .results & {
    border: 1px solid #d4d4d4;
    width: calc(100% - 30px);
  }
  li {
    width: 100%;
    padding: 11px 0.7em;
    border-bottom: 1px solid #d4d4d4;
    outline: none;
    cursor: pointer;
    background-color: rgba(255, 255, 255, .88);
    font-family: 'MuseoSans300';
    color: #3B3B3B;
    border-top: none;
    margin: 0;
    &:last-child {
      border-bottom: none;
    }
    &.active,
    &:hover {
      background-color: #009FE3;
      color: white;
    }
    &.no-address {
      font-family: 'MuseoSans500';
      color: black;
      font-size: .95em;
      a,
      a:hover,
      a:focus {
        color: #00ace5;
        border-bottom: none;
      }
      &.active {
        background-color: #009FE3;
        color: #3B3B3B;
        a {
          color: white;
        }
      }
      &:hover {
        background-color: white;
      }
    }
  }
}

@media (min-width: 768px) {
  .bbc-address-autocomplete {
    input {
      width: 470px;
      float: left;
      font-size: 17px;
    }

    .btn-address-search-action {
      float: right;
      margin-top: 0;
    }
  }
}


@media (max-width: 768px) {
  .bbc-address-autocomplete {
    input {
      width: 100%;
      float: left;
    }

    .btn-address-search-action {
      float: none;
      margin-top: 40px;
    }
  }
  .ar-suggestions {
    width: calc(100% - 30px);
  }
}

@media (min-width: 481px) and (max-width: 768px){
  .ar-suggestions {
    margin-top: -88px;
  }
  .results .ar-suggestions {
    margin-top: 2px;
  }
}

.btn-address-search-action {
  float: right;
  height: 50px;
  background-color: #35a6e8;
  line-height: 100%;
  color: #fff;
  font-family: 'MuseoSans300', sans-serif;
  border-radius: 36px;
  -webkit-transition: all .3s cubic-bezier(.445, .05, .55, .95);
  transition: all .3s cubic-bezier(.445, .05, .55, .95);
  text-decoration: none;
  -webkit-appearance: none;
  outline: none;
  padding: 10px 30px;
  font-size: 14px;
  -webkit-transition: all .2s cubic-bezier(.445, .05, .55, .95);
  transition: all .2s cubic-bezier(.445, .05, .55, .95);
}




