﻿:global .bbc-dial {
	width: 16.375em;
	position: relative;
    margin: 0 auto 3em;
	h3.no-connection { // No connection
		color: #666;
        font-family: 'MuseoSans500';
		padding: 0 2em;
		font-size: 1em;

		i.fa {
			color: #009FE3;
			display: block;
			font-size: 3.4em;
			margin-bottom: 0.25em;
		}
	}
	
	img {
		width: 100%;
	}
	
	.info {
		position: absolute;
		left: 4em;
		right: 4em;
		top: 4em;
		height: 8em;
		text-align: center;
		line-height: 1em;
        font-family: 'MuseoSans700';
        color: #4C647B;
		span {
			font-size: 1.5em;
		}
        .up-to {
          font-family: 'MuseoSans500';
        }
		
		.speed,
		.vdsl {
			display: block;
			font-size: 4.75em;
			line-height: 1em;
			position: relative;
			top: 0.05em;
		}
		.vdsl {
			font-size: 2.15em;
			margin: 0.4em 0 0 0.1em;
		}
        .unit {
          display: block;
          margin-top: .275em;
          font-size: 0.875em;
          font-family: 'MuseoSans300';
        }

		.vdsl-more a {
			text-decoration: none;
			color: inherit;
			border-bottom: 1px dotted rgba(255,255,255,0.5);
			&:hover {
				border-bottom-style: solid;
			}
		}
	}
}







