// basic mixins used to try to standardize style
// font size
$font-size: (
        xxl:              42px,
        xl:               30px,
        l:                18px,
        m:                16px,
        s:                14px,
        xs:               12px
);
@mixin h1() {
  margin: 1em 0;
  font-family: 'MuseoSans100';
  font-size: 2.625em;
  font-weight: normal;
  line-height: 1.3em;
  color: $text-colour-primary;
  text-align: center;
}
@mixin h2() {
  margin: .7em 0;
  font-family: 'MuseoSans100';
  font-size: 1.875em;
  font-weight: normal;
  line-height: 1.3em;
  color: $text-colour-primary;
  text-align: center;
}
@mixin h3() {
  margin: 0;
  font-family: 'MuseoSans100';
  font-size: 1.875em;
  font-weight: normal;
  line-height: 1.375em;
  color: $text-colour-primary;
  text-align: center;
}
@mixin h4() {
  margin: 0;
  font-family: 'MuseoSans500';
  font-size: 1.375em;
  font-weight: normal;
  line-height: 1.5em;
  color: $text-colour-primary;
  text-align: left;
}


