:global .chorus-shell-wrapper {

	// How components fit in the shell wrapper (layout)

	.bbc-progress-bar {
		margin-top: 0;
	}
	@media(max-width: 27em) {
		.bbc-progress-bar { display: none; }
	}


	.step {
		margin: 0em auto;
		// max-width: 46.875em;
		// box-shadow: 0 0.1em 0.2em rgba(0,0,0,0.2);
		@media(max-width: 30em) {
			margin: 1em auto;
			box-shadow: none;
		}
	}

	.step-actions {
		margin: 1em auto 3em auto;
		max-width: 45em;
		padding: 0 1em;
		overflow: hidden;
		@media(max-width: 50em) {
			max-width: 25em;
		}


		button {
			outline: none;
			text-transform: none;
			&.next {
				float: right;
				.fa { margin-left: 0.6em; }
			}
			&.prev {
				float: left;
				.fa { margin-right: 0.6em; }
			}

			@media(max-width: 50em) {
				padding: 0.65em 1.5em;
				.fa {
					margin: 0 0.6em;
					font-size: 1.4em;
				}
			}
		}
	}

	.step-actions.single-action {
		text-align: center;
		overflow: visible;

		button {
			float: none;
			min-width: 167px;
		}
	}


	.chorus-btn {
		padding: .4em;
		background: white;
		text-transform: uppercase;
		color: #36A8FC;
		height: 2.7em;
		min-width: 167px;
		font-family: 'MuseoSans500';
		box-shadow: none !important;
		letter-spacing: 0.5px;
		line-height: 1.5rem;
		display: inline-block;
		font-weight: normal;
		line-height: 1.25;
		text-align: center;
		vertical-align: middle;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		border: 2px solid transparent;
		padding: 0.33em 1.45em;
		font-size: 0.9em;
		border-radius: 20px;
		transition: all 0.2s ease-in-out;
		@media(max-width: 25em){
			min-width: 45%;
		}
		&:hover {
			background-color: #36A8FC;
			color: white;
		}
		&.chorus-btn-blue {
			color: #fff;
			background-color: #36A8FC;
			border-color: #36A8FC;
		}
	}

	#colophon {
		padding-top: 0;
		margin-top: 0;
	}
	article {
		margin: 0;
	}
}


